import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { selectUser, useSelector } from '../app/redux/exports';
import axios from 'axios';
import { endpoints } from '../common/Endpoints';
import LoadingSpinner from '../common/LoadingSpinner';
import { useToast } from '../common/CustomToast';

const Listing = () => {
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState([]);
    const [listing, setListing] = useState({});

    const user = useSelector(selectUser);
    let { id } = useParams();

    const getListing = async (id) => {
        setLoading(true);

        await axios.get(endpoints.listing.listing(id))
            .then(res => {
                setLoading(false);
                setListing(res.data);
            })
            .catch(e => {
                setLoading(false);
                setErrors(['Error loading listings.']);
            });
    }

    useEffect(() => {
        setListing(getListing(id));
    }, []);

    return (
        <Container>
            <h1>Listing: {LoadingSpinner(loading)} {!loading && listing.title}</h1>
        </Container>
    );
}

export default Listing;