import React, { useState, useEffect } from 'react';
import { selectUser, useSelector, selectWishlist, addWishlist, removeWishlist, useDispatch } from '../app/redux/exports';
import { endpoints, jsonConfig } from '../common/Endpoints';
import { Button, Spinner } from 'react-bootstrap';
import { useToast } from '../common/CustomToast';
import axios from 'axios';

const WishlistButton = ({ listingId }) => {
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState([]);

    const user = useSelector(selectUser);
    const showToast = useToast();
    const wishlist = useSelector(selectWishlist);
    const dispatch = useDispatch();

    if (!user?.loggedIn)
        return null;

    const inWishlist = wishlist?.wishlists?.includes(listingId) ?? false;

    const handleWishlistClick = async () => {
        setLoading(true);
        const endpoint = inWishlist ? endpoints.wishlist.remove : endpoints.wishlist.add;
        await axios.post(endpoint, listingId, jsonConfig(user.token))
            .then(res => {
                setLoading(false);
                if (!res.data.didSave) {
                    showToast({ body: res.data.errors[0], type: 'danger', header: 'Error!' });
                    return;
                }

                dispatch(inWishlist ? removeWishlist(listingId) : addWishlist(listingId))
                showToast({ body: inWishlist ? 'Removed listing from wishlist.' : 'Added listing to wishlist.' });
            })
            .catch(e => {
                setLoading(false);
                setErrors(['Error adding to wishlist.']);
            });
    }

    return (
        <Button variant="primary" size='sm' onClick={handleWishlistClick} disabled={loading}>
            {loading ? <Spinner
                as="span"
                animation="border"
                size="sm"
                role="status"
                aria-hidden="true" /> : inWishlist ? <span>Unwishlist</span> : <span>Wishlist</span>}
        </Button>
    );
}

export default WishlistButton;