import React, { useState } from 'react';
import { Container, Nav, Navbar, Button, Spinner, NavDropdown } from 'react-bootstrap';
import { useDispatch, useSelector, selectUser, logout } from '../app/redux/exports';
import { endpoints, jsonConfig } from '../common/Endpoints';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const CustomNavbar = () => {
    const [loading, setLoading] = useState(false);

    const user = useSelector(selectUser);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const dropdownActive = ['#/profile','#/wishlist','#/mylistings'].includes(window.location.hash);

    const handleLogout = async (e) => {
        e.preventDefault();
        setLoading(true);


        await axios.post(endpoints.user.logout, null, jsonConfig(user.token))
            .then(res => {
                setLoading(false);
                if (!res.data.didSave) {
                    return;
                }

                dispatch(logout());
                navigate('/home');
            });
    }

    return (
        <Navbar collapseOnSelect expand='md' variant="dark">
            <Container>
                <Navbar.Brand>Columbus Trade</Navbar.Brand>
                <Navbar.Toggle />
                <Navbar.Collapse>
                    <Nav className='me-auto' activeKey={window.location.hash}>
                        <Nav.Link href='#/home'>Home</Nav.Link>
                        {user?.loggedIn ?
                            <Nav.Link href='#/newlisting'>New Listing</Nav.Link>
                            : <div></div>
                        }
                        {user?.loggedIn ?
                            <NavDropdown menuVariant='dark' title="Profile" id="nav-dropdown" active={dropdownActive}>
                                <NavDropdown.Item href='#/profile'>My Profile</NavDropdown.Item>
                                <NavDropdown.Item href='#/wishlist'>My Wishlist</NavDropdown.Item>
                                <NavDropdown.Item href='#/mylistings'>My Listings</NavDropdown.Item>
                            </NavDropdown>
                            : <div></div>
                        }
                    </Nav>
                </Navbar.Collapse>
                <Navbar.Collapse className="justify-content-end">
                    {user?.loggedIn ? (
                        <>
                            <Navbar.Text>
                                Signed in: <a href="#/profile">{user.email}</a>
                            </Navbar.Text>
                            <Button variant="primary" onClick={handleLogout} disabled={loading}>
                                {loading ? <Spinner
                                    as="span"
                                    animation="border"
                                    size="sm"
                                    role="status"
                                    aria-hidden="true" /> : <span>Logout</span>}
                            </Button>
                        </>
                    ) : (
                        <>
                            <Button href='#/login'>Login</Button>
                            <Button href='#/register'>Register</Button>
                        </>
                    )}
                        
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default CustomNavbar;