import React, { useState, useEffect } from 'react';
import { Container, Button, Table, Badge } from 'react-bootstrap';
import { selectUser, useSelector } from '../app/redux/exports';
import { endpoints } from '../common/Endpoints';
import axios from 'axios';
import LoadingSpinner from '../common/LoadingSpinner';
import WishlistButton from '../wishlist/WishlistButton';
import WishlistProvider from '../wishlist/WishlistProvider';

const Home = () => {
    const [errors, setErrors] = useState([]);
    const [loading, setLoading] = useState(true);
    const [listings, setListings] = useState([]);

    const user = useSelector(selectUser);

    const getListings = async () => {
        await axios.get(endpoints.listing.listings)
            .then(res => {
                setLoading(false);
                setListings(res.data);
            })
            .catch(e => {
                setErrors(['Error loading listings.']);
            });
    }

    useEffect(() => {
        getListings();
    }, []);

    return (
        <Container>
            <h1>Recently listed items {user?.loggedIn && !loading &&
                    <Button href='#/newlisting' disabled={loading}>Add New</Button>} {LoadingSpinner(loading)} </h1>

            {listings?.length > 0 && renderListings(listings)}
        </Container>
    );    

}

const renderListings = (listings) => {
    return (
        <Table striped bordered hover variant='dark'>
            <thead>
                <tr>
                    <th>Title</th>
                    <th>Description</th>
                    <th>Price</th>
                    <th>Tags</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <WishlistProvider listings={listings} />
            </tbody>
        </Table>
    );
}


export default Home;