import { useEffect, useState } from 'react';
import { HubConnectionBuilder } from '@microsoft/signalr';
import { useToast } from '../common/CustomToast';
import { selectUser, useSelector } from '../app/redux/exports';
import { base } from '../common/Endpoints';

export const useNotifications = () => {
    const [connection, setConnection] = useState(null);

    const user = useSelector(selectUser);
    const toast = useToast();

    useEffect(() => {
        if (!user?.loggedIn) {
            if (connection?.state === "Connected") {
                connection.stop();
            }

            return;
        }

        const connect = new HubConnectionBuilder()
            .withUrl(`${base}/notifications?userId=${user.hashId}`)
            .withAutomaticReconnect()
            .build();

        setConnection(connect);
    }, [user]);

    useEffect(() => {
        if (!connection)
            return;

        connection
            .start()
            .then(() => {
                connection.on("NewNotification", (message) => onNotification(message));
            })
            .catch((error) => console.log(error));
    }, [connection])

    const onNotification = (message) => {
        toast({ body: message, header: 'New Notification' });
    }

    return connection;
}