import { createSlice } from '@reduxjs/toolkit';

export const toastSlice = createSlice({
    name: 'toast',
    initialState: {
        toasts: []
    },
    reducers: {
        fireToast: (state, action) => {
            state.toasts = [...state.toasts, action.payload];
        },
        removeToast: (state, action) => {
            let toast = state.toasts.find(x => x.id == action.payload);
            toast.show = false;
        },
        clearToast: (state, action) => {
            state.toasts = state.toasts.filter(o => o.id > action.payload);
        }
    }
});

export const { fireToast, removeToast, clearToast } = toastSlice.actions;
export const selectToast = (state) => state.toast;
export default toastSlice.reducer;