import React, { useState } from 'react';
import { Container, Form, Button, Row, Spinner } from 'react-bootstrap';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { selectUser, useSelector, login, useDispatch } from '../app/redux/exports';
import ErrorAlert from '../common/ErrorAlert';
import { endpoints } from '../common/Endpoints';
import { useToast } from '../common/CustomToast';

const Register = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errors, setErrors] = useState([]);
    const [loading, setLoading] = useState(false);

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const showToast = useToast();
    const user = useSelector(selectUser);

    const handleRegister = async (e) => {
        e.preventDefault();
        setErrors([]);
        setLoading(true);

        const newUser = {
            email: email,
            password: password
        };

        await axios.post(endpoints.user.register, newUser)
            .then(response => {
                setLoading(false);

                if (!response.data.didSave) {
                    setErrors(response.data.errors)
                    return;
                }
                dispatch(
                    login({
                        email: email,
                        token: response.data.value.accessToken,
                        hashId: response.data.value.hashId,
                        loggedIn: true
                    })
                );

                showToast({ body: 'Created new account.' });
                navigate('/home');
            })
            .catch(e => {
                setErrors(["Error saving new user."]);
            });
    }

    if (user?.loggedIn) {
        return ErrorAlert({heading: 'Error', errors: ["You're already logged in."]});
    }

    return (
        <Container className='my-auto'>
            <h1>Register</h1>
            {errors.length ? ErrorAlert({heading: 'Error', errors: errors}) : <div></div>}
            <Row className="row-center">
                <Form onSubmit={handleRegister}>
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email:</Form.Label>
                        <Form.Control required maxLength='128' type="email" placeholder="Enter an @columbus.edu email" onChange={(e) => setEmail(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Password:</Form.Label>
                        <Form.Control required maxLength='128' type="password" placeholder="Enter password" onChange={(e) => setPassword(e.target.value)} />
                    </Form.Group>
                    <Button variant="primary" type="submit" disabled={loading}>
                        {loading ? <Spinner
                            as="span"
                            animation="border"
                            size="sm"
                            role="status"
                            aria-hidden="true" /> : <span>Register</span>}
                    </Button>
                </Form>
            </Row>
        </Container>
    );
}


export default Register;