import '../global.css';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { login, selectUser, useSelector } from '../app/redux/exports';
import { Container, Form, Button, Alert, Row, Spinner } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import ErrorAlert from '../common/ErrorAlert';
import axios from 'axios';
import { endpoints, jsonConfig } from '../common/Endpoints';

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errors, setErrors] = useState([]);
    const [loading, setLoading] = useState(false);

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const user = useSelector(selectUser);

    const handleLogin = async (e) => {
        e.preventDefault();
        setErrors([]);
        setLoading(true);

        await axios.post(endpoints.user.login, { email, password }, jsonConfig())
            .then(res => {
                setLoading(false);

                if (!res.data.didSave) {
                    setErrors(res.data.errors);
                    return;
                }

                dispatch(
                    login({
                        email: email,
                        token: res.data.value.accessToken,
                        hashId: res.data.value.hashId,
                        loggedIn: true
                    }));

                navigate('/home');
            })
            .catch(e => setLoading(false));
    }

    if (user?.loggedIn) {
        return ErrorAlert({ heading: 'Error', errors: ["You're already logged in."] });
    }

    return (
        <Container className='styled-container'>
            <h1>Login</h1>
            {errors.length ? ErrorAlert({ heading: 'Error', errors: errors }) : <div></div>}
            <Row className="row-center">
                <Form onSubmit={handleLogin}>
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email:</Form.Label>
                        <Form.Control required maxLength='128' type="email" placeholder="Enter your @columbus.edu email" onChange={(e) => setEmail(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Password:</Form.Label>
                        <Form.Control required maxLength='128' type="password" placeholder="Enter password" onChange={(e) => setPassword(e.target.value)} />
                    </Form.Group>
                    <Button variant="primary" type="submit" disabled={loading}>
                        {loading ? <Spinner
                            as="span"
                            animation="border"
                            size="sm"
                            role="status"
                            aria-hidden="true" /> : <span>Login</span> }
                    </Button>
                </Form>
            </Row>
        </Container>
    );
}

export default Login;