import React, { useState, useEffect } from 'react';
import { ToastContainer, Toast } from 'react-bootstrap';
import { useSelector, useDispatch, selectToast, fireToast, removeToast, clearToast } from '../app/redux/exports';

const useHideToast = () => {
    const dispatch = useDispatch();
    return (id) => {
        dispatch(removeToast(id));
    }
}

let id = 0;
export const useToast = () => {
    const dispatch = useDispatch();
    return (props) => {
        if (id > 50) {
            dispatch(clearToast(id));
            id = 0;
        }

        dispatch(
            fireToast({
                id: id++,
                show: props?.show ?? true,
                delay: props?.delay ?? 3000,
                body: props?.body ?? '',
                type: props?.type ?? 'success',
                header: props?.header ?? 'Success!'
            })
        );
    }
}

const CustomToast = () => {
    const [scrollPosition, setScrollPosition] = useState(0);

    const handleScroll = () => {
        setScrollPosition(window.pageYOffset);
    }

    const hideToast = useHideToast();
    const toast = useSelector(selectToast);
    const stopShow = (toast) => {
        hideToast(toast.id);
    }

    useEffect(() => {
        window.addEventListener('scroll', handleScroll, { passive: true });

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <ToastContainer className="p-3" position='top-end' style={{marginTop: scrollPosition}}>
            {toast.toasts.map((toast, i) =>
                <Toast key={i} bg={toast.type} show={toast?.show} onClose={_ => stopShow(toast)} delay={toast.delay} autohide>
                    <Toast.Header>
                        <strong className="me-auto">{toast.header}</strong>
                    </Toast.Header>
                    <Toast.Body>{toast.body}</Toast.Body>
                </Toast>
            )}
        </ToastContainer>
    );
}

export default CustomToast;