import React, { useState, useEffect } from 'react';
import { Container, Button } from 'react-bootstrap';
import { selectUser, useSelector } from '../app/redux/exports';
import axios from 'axios';
import { endpoints, jsonConfig } from '../common/Endpoints';
import LoadingSpinner from '../common/LoadingSpinner';
import { useToast } from '../common/CustomToast';

const Wishlist = () => {
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState([]);
    const [wishlist, setWishlist] = useState({});

    const user = useSelector(selectUser);

    return (
        <Container>
            <h1> Wishlist </h1>
        </Container>
    );
}

export const getWishlist = async (token) => {
    if (token == null)
        return [];
    return await axios.post(endpoints.wishlist.wishlist, null, jsonConfig(token));
}

export default Wishlist;