import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import { Route, Routes, HashRouter as Router, Navigate } from 'react-router-dom';
import React from 'react';
import Home from '../home/Home';
import Login from '../login/Login';
import Register from '../register/Register';
import Wishlist from '../wishlist/Wishlist';
import CustomNavbar from '../navbar/Navbar';
import Profile from '../profile/Profile';
import ProtectedRoute from './ProtectedRoute';
import Listing from '../listing/Listing';
import NewListing from '../listing/NewListing';
import { selectUser, useSelector, login, logout, useDispatch } from '../app/redux/exports';
import { endpoints, jsonConfig } from '../common/Endpoints';
import CustomToast from '../common/CustomToast';
import { useNotifications } from '../common/Notifications';

const App = () => {
    const dispatch = useDispatch();
    const user = useSelector(selectUser);
    const notifications = useNotifications();


    if (user?.token.length == 128 && user?.email == null) {
        axios.post(endpoints.user.info, null, jsonConfig(user.token))
            .then(res => {
                if (res.data.hashId != null) {
                    dispatch(
                        login({
                            email: res.data.email,
                            hashId: res.data.hashId,
                            loggedIn: res.data.isLoggedIn,
                            token: user.token
                        })
                    )
                }
                else {
                    dispatch(logout())
                }
            })
            .catch(dispatch(logout()));
    }


    return (
        <div className='App bg-dark' style={{height: '100%'}}>
            <Router>
                <CustomNavbar />
                <CustomToast />
                <Routes>
                    <Route path={`/*`} element={<Navigate to='/home'/>} />
                    <Route path={`/register`} element={<Register />} />
                    <Route path={`/login`} element={<Login />} />
                    <Route path={`/home`} element={<Home />} />
                    <Route path={`/listing/:id`} element={<Listing />} />
                    <Route path={`/wishlist`} element={
                        <ProtectedRoute user={user}>
                            <Wishlist />
                        </ProtectedRoute>
                    } />
                    <Route path={`/profile`} element={
                        <ProtectedRoute user={user}>
                            <Profile />
                        </ProtectedRoute>
                    } />
                    <Route path={`/newlisting`} element={
                        <ProtectedRoute user={user}>
                            <NewListing />
                        </ProtectedRoute>
                    } />
                </Routes>
            </Router>
        </div>
    );
}

export default App;