export const base = process.env.NODE_ENV === 'development' ?
    'https://localhost:7189' :
    'https://group-project.azure-api.net/4330';

export const jsonConfig = (accessToken) => {
    return accessToken?.length == 128 ? {
        headers: {
            'content-type': 'application/json',
            '4330-access-token': accessToken
        }
    } : {
        headers: {
            'content-type': 'application/json',
        }
    }
};

export const endpoints = {
    user: {
        login: `${base}/user/login`,
        logout: `${base}/user/logout`,
        register: `${base}/user/register`,
        info: `${base}/user/info`
    },
    listing: {
        listings: `${base}/listing/listings`,
        add: `${base}/listing/add`,
        listing: (id) => `${base}/listing/listing/${id}`
    },
    wishlist: {
        add: `${base}/wishlist/add`,
        remove: `${base}/wishlist/remove`,
        wishlist: `${base}/wishlist/wishlist`,        
    }
}