import React, { useState, useEffect } from 'react';
import WishlistButton from '../wishlist/WishlistButton';
import { Badge, Button } from 'react-bootstrap';
import { getWishlist } from './Wishlist';
import { selectUser, useSelector, useDispatch, setWishlist } from '../app/redux/exports';

const WishlistProvider = ({ listings }) => {
    const [loading, setLoading] = useState(false);

    const user = useSelector(selectUser);
    const dispatch = useDispatch();

    useEffect(() => {
        async function updateWishlist() {
            if (user?.token.length === 128) {
                setLoading(true);
                await getWishlist(user.token)
                    .then(res => {
                        setLoading(false);
                        dispatch(
                            setWishlist(res.data?.map(x => x.listingId) ?? [])
                        );
                    });
            }
        }

        updateWishlist();
    }, [user])

    return (
        listings.map((item, i) =>
            <tr key={i}>
                <td style={{ width: '175px' }}>{item.title}</td>
                <td style={{ width: '300px' }}>{item.description}</td>
                <td style={{ width: '100px' }}>${item.price}</td>
                <td style={{ width: '175px' }}>{item.tags.map((tag, i) =>
                    <Badge key={`tag-${i}`} style={{ marginLeft: '5px' }} pill bg='primary'>{tag}</Badge>
                )}</td>
                <td style={{ textAlign: 'center', width: '175px' }}>
                    <Button size='sm' href={`#/listing/${item.id}`}>View</Button>
                    {!loading && <WishlistButton listingId={item.id} />}
                </td>
            </tr>
        )
    );
}

export default WishlistProvider;