import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container, Form, Spinner, Button, Row, InputGroup, Badge } from 'react-bootstrap';
import { selectUser, useSelector } from '../app/redux/exports';
import { endpoints, jsonConfig } from '../common/Endpoints';
import { useToast } from '../common/CustomToast';
import axios from 'axios';

const NewListing = () => {
    const [loading, setLoading] = useState(false);
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    const [price, setPrice] = useState(0.0);
    const [contactInfo, setContactInfo] = useState('');
    const [tags, setTags] = useState([]);
    const [errors, setErrors] = useState([]);

    const user = useSelector(selectUser);
    const navigate = useNavigate();
    const showToast = useToast();

    const handleNewListing = async (e) => {
        e.preventDefault();
        setErrors([]);
        setLoading(true);

        const listing = {
            title: title,
            description: description,
            price: price,
            tags: tags,
            contactInfo: contactInfo,
            userCrte: user.hashId
        }

        await axios.post(endpoints.listing.add, listing, jsonConfig(user.token))
            .then(res => {
                setLoading(false);
                if (!res.data.didSave) {
                    setErrors(res.data.errors);
                    return;
                }

                showToast({body: 'Added new listing.'});
                navigate('/home');
            })
            .catch(e => setLoading(false));
    }

    const handleTags = (e) => {
        setTags(e.trim().split(','));
    }

    return (
        <Container>
            <h1>New Listing</h1>
            <Row className="row-center">
                <Form onSubmit={handleNewListing}>
                    <Form.Group className="mb-3">
                        <Form.Label>Title:</Form.Label>
                        <Form.Control required maxLength='50' type="text" placeholder="Enter a title" onChange={(e) => setTitle(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Description:</Form.Label>
                        <Form.Control required maxLength='255' as='textarea' placeholder="Enter a description" onChange={(e) => setDescription(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Price:</Form.Label>
                        <InputGroup className="mb-3">
                            <InputGroup.Text>$</InputGroup.Text>
                            <Form.Control required placeholder='Enter a price' type='number' step='0.01' onChange={(e) => setPrice(e.target.value)} />
                        </InputGroup>
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Contact Information:</Form.Label>
                        <Form.Control required maxLength='255' as='textarea' placeholder="Enter your contact information" onChange={(e) => setContactInfo(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Tags:</Form.Label>
                        {tags.map((item, i) =>
                            <Badge key={i} style={{marginLeft: '5px'}} pill bg='primary'>{item}</Badge>
                        )}
                        <Form.Control style={{marginTop: '3px'}} required maxLength='50' type='text' placeholder="Enter comma separated tags" onChange={(e) => handleTags(e.target.value)} />
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>Images (Not Implemented):</Form.Label>
                        <Form.Control type="file" disabled />
                    </Form.Group>
                    <Button variant="primary" type="submit" disabled={loading}>
                        {loading ? <Spinner
                            as="span"
                            animation="border"
                            size="sm"
                            role="status"
                            aria-hidden="true" /> : <span>Submit</span>}
                    </Button>
                    <div style={{marginBottom: '20px'}}></div>
                </Form>
            </Row>
        </Container>
    );
}

export default NewListing;